.flexibleA-editor {
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  &__inner{
    max-width:600px;
    padding:30px 20px;
    display:block;
    margin:auto;
    background-color:rgba(255, 255, 255, 0.8);
  }
  p{
    font-size: 16px;
  }
}
